  /* 3D Card Flip */

  /* entire container, keeps perspective */

  .flip-wrap {
    width: 100%;
    position: relative;
    height: 600px;
    overflow: hidden;

    @media (--phone-viewport) {
      height: 400px;
    }

  .flip-container {
    perspective: 1000px;
    float: left;
    position: absolute;
    top: 0;

    &:nth-child(2n) {
      top: 40px;
      left: 180px;

      @media (--phone-viewport) {
        left: 90px;

        img {
          width: 90px;
        }
      }
    }

    &:nth-child(3n) {
      top: 0;
      left: 360px;

      @media (--phone-viewport) {
        left: 180px;

        img {
          width: 90px;
        }
      }
    }

    &:nth-child(4n) {
      top: 40px;
      left: 540px;

      @media (--phone-viewport) {
        left: 270px;

        img {
          width: 90px;
        }
      }
    }

    &:nth-child(5n) {
      top: 0;
      left: 720px;

      @media (--phone-viewport) {
        left: 360px;

        img {
          width: 90px;
        }
      }
    }

    &:nth-child(6n) {
      top: 40px;
      left: 900px;

      @media (--phone-viewport) {
        left: 450px;

        img {
          width: 90px;
        }
      }
    }

    &:nth-child(7n) {
      top: 0;
      left: 1080px;

      @media (--phone-viewport) {
        display: none;
      }
    }

    &:nth-child(8n) {
      top: 40px;
      left: 1260px;

      @media (--phone-viewport) {
        display: none;
      }
    }

    &:nth-child(9n) {
      top: 0;
      left: 1440px;

      @media (--phone-viewport) {
        display: none;
      }
    }

    &:nth-child(10n) {
      top: 40px;
      left: 1620px;

      @media (--phone-viewport) {
        display: none;
      }
    }

    &:nth-child(11n) {
      top: 0;
      left: 1800px;

      @media (--phone-viewport) {
        display: none;
      }
    }

    &:hover .flipper, &.hover .flipper, &:hover .flipper-b, &.hover .flipper-b,
    &:hover .flipper-c, &.hover .flipper-c, &:hover .flipper-d, &.hover .flipper-d,
    &:hover .flipper-e, &.hover .flipper-e, &:hover .flipper-f, &.hover .flipper-f,
    &:hover .flipper-g, &.hover .flipper-g, &:hover .flipper-h, &.hover .flipper-h,
    &:hover .flipper-i, &.hover .flipper-i, &:hover .flipper-j, &.hover .flipper-j,
    &:hover .flipper-k, &.hover .flipper-k, {
      transform: rotateY(180deg);
    }

    /* flip speed goes here */
    .flipper, .flipper-b, .flipper-c, .flipper-d, .flipper-e, .flipper-f, .flipper-g, .flipper-h, .flipper-i, .flipper-j, .flipper-k  {
      transition: 0.6s;
      transform-style: preserve-3d;
      position: relative;

      /* hide back of pane during swap */
      .front, .back {
        backface-visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }

      /* front pane, placed above back */
      .front {
        z-index: 2;
        /* for firefox 31 */
        transform: rotateY(0deg);
        box-shadow: 10px 10px 39px 0px rgba(0,0,0,0.75);
      }

      /* back, initially hidden pane */
      .back {
        transform: rotateY(180deg);
        box-shadow: 10px 10px 39px 0px rgba(0,0,0,0.75);
      }

    }
  }

  .flip-container, .front, .back {
    width: 180px;
    height: 500px;

    @media (--phone-viewport) {
      width: 90px;
      height: 250px;

      img {
        width: 90px;
      }
    }
  }

